@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Verdana", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.navigation {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .navigation.active {
    justify-content: flex-start;
    flex-direction: column;
    padding: 20px 15px;
    padding-top: 0;
    position: fixed;
    top: 0;
    right: 0;
    width: 89%;
    height: 100vh;
    background-color: #f9fbfd;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  .navigation.active .navigation__button {
    top: 13px;
    right: 2px;
  }
  .navigation.active .navigation__button::before, .navigation.active .navigation__button::after {
    top: 45%;
    left: 25%;
    width: 18px;
    height: 1px;
    background-color: #011c40;
  }
  .navigation.active .navigation__button::before {
    transform: rotate(45deg);
  }
  .navigation.active .navigation__button::after {
    transform: rotate(-45deg);
  }
  .navigation.active .navigation__link {
    color: #011c40;
    padding: 0;
  }
  .navigation.active .navigation__link svg {
    fill: currentColor;
  }
  .navigation.active .navigation__link:focus, .navigation.active .navigation__link:hover {
    opacity: 0.7;
  }
  .navigation.active .navigation__link:active {
    fill: #29a3cc;
  }
  .navigation.active .navigation__list {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 8px;
    margin-top: 6px;
    margin-left: 12px;
  }
  .navigation.active .navigation__item a {
    color: #011c40;
  }
  .navigation.active .navigation__item a:focus, .navigation.active .navigation__item a:hover {
    opacity: 0.7;
  }
  .navigation.active .navigation__item a:active {
    color: #29a3cc;
  }
}

.navigation__link:focus, .navigation__link:hover {
  opacity: 0.7;
}

@media (max-width: 1023px) {
  .navigation__logo {
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .navigation__logo {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
.navigation__logo svg {
  fill: #f9fbfd;
}
.navigation__logo svg:active {
  fill: #29a3cc;
}
@media (max-width: 1023px) {
  .navigation__logo svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .navigation__logo svg {
    width: 42px;
    height: 22px;
  }
}
@media (max-width: 1023px) {
  .navigation__logo--header {
    padding-top: 6px;
  }
}
@media (max-width: 767px) {
  .navigation__logo--header {
    padding-top: 20px;
  }
}
.navigation__logo--footer {
  margin-right: 40px;
}
@media (max-width: 1023px) {
  .navigation__logo--footer {
    margin-right: 24px;
  }
}
@media (max-width: 767px) {
  .navigation__logo--footer {
    padding-bottom: 18px;
    margin-right: 0;
    margin-bottom: 0;
  }
}

.navigation__list {
  display: grid;
  grid-template-columns: repeat(4, max-content);
  gap: 0 33px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .navigation__list {
    gap: 0 24px;
  }
}
@media (max-width: 767px) {
  .navigation__list--header {
    display: none;
  }
}
.navigation__list--footer {
  margin: 0 auto 0 0;
  gap: 0 31px;
}
@media (max-width: 1023px) {
  .navigation__list--footer {
    gap: 23px;
  }
}
@media (max-width: 767px) {
  .navigation__list--footer {
    width: 100%;
    grid-template-columns: 1fr;
    gap: 11px;
    margin-bottom: 22px;
  }
}

.navigation__item {
  max-width: 175px;
}
@media (max-width: 1023px) {
  .navigation__item {
    max-width: 151px;
  }
}
@media (max-width: 767px) {
  .navigation__item {
    max-width: 100%;
  }
}
.navigation__item a {
  user-select: none;
  display: block;
  padding: 10px 0;
  font-family: "Montserrat", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .navigation__item a {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .navigation__item a {
    font-size: 18px;
    line-height: 22px;
  }
}
.navigation__item a:focus, .navigation__item a:hover {
  opacity: 0.7;
}
.navigation__item a:active {
  color: #29a3cc;
}
@media (max-width: 1023px) {
  .navigation__item--footer a {
    padding-bottom: 3px;
    padding-top: 3px;
    font-size: 12px;
    line-height: 15px;
  }
}
@media (max-width: 767px) {
  .navigation__item--footer a {
    font-size: 12px;
    line-height: 15px;
  }
}

.navigation__button {
  display: none;
}
@media (max-width: 767px) {
  .navigation__button {
    position: absolute;
    display: block;
    top: 10px;
    right: -8px;
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: none;
  }
  .navigation__button::before, .navigation__button::after {
    content: "";
    position: absolute;
  }
  .navigation__button::before {
    top: 36%;
    left: 21%;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
  }
}

.navigation__overlay {
  display: none;
}

.navigation.active ~ .navigation__overlay {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 11%;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(0, 43, 102, 0.5);
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    padding: 4px 0 48px;
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label,
.custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error,
.custom-textarea__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;
  display: flex;
  width: 500px;
