  font-family: "Montserrat", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #011c40;
}
@media (max-width: 1023px) {
  .form__content {
    padding: 88px 163px 100px;
    grid-template-columns: 1fr;
    gap: 53px;
  }
}
@media (max-width: 767px) {
  .form__content {
    padding-top: 60px;
    padding-bottom: 65px;
    gap: 32px;
  }
}

.form__description {
  padding-top: 163px;
  margin-right: 20px;
}
.form__description br {
  display: none;
}
@media (max-width: 767px) {
  .form__description br {
    display: block;
  }
}
.form__description h2 {
  margin: 0;
  margin-bottom: 9px;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
}
@media (max-width: 1023px) {
  .form__description h2 {
    margin-bottom: 4px;
    font-size: 34px;
    line-height: 51px;
  }
}
@media (max-width: 767px) {
  .form__description h2 {
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    max-width: 286px;
  }
}
.form__description p {
  margin: 0;
  max-width: 352px;
}
@media (max-width: 1023px) {
  .form__description p {
    text-align: center;
  }
}
@media (max-width: 767px) {
  .form__description p {
    max-width: 440px;
    font-size: 16px;
    line-height: 23px;
  }
}
@media (max-width: 1023px) {
  .form__description {
    margin-right: 0;
    padding-top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.form__wrapper {
  width: 403px;
  padding: 45px 50px 50px;
  margin-left: auto;
  background-color: #011c40;
}
.form__wrapper h3 {
  margin: 0;
  margin-bottom: 26px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__wrapper h3 {
    line-height: 26px;
    text-align: center;
  }
}
.form__wrapper span {
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__wrapper span {
    line-height: 12px;
  }
}
@media (max-width: 1023px) {
  .form__wrapper {
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .form__wrapper {
    width: 100%;
    padding: 34px 30px 40px;
  }
}

.form__inputs-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 19px;
}
@media (max-width: 767px) {
  .form__inputs-wrapper {
    gap: 20px;
  }
}

.form__input {
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  position: relative;
}
.form__input input:not([type=checkbox]) {
  width: 100%;
  background-color: #f9fbfd;
  border: none;
  padding: 14px 16px;
}
.form__input input:not([type=checkbox])::placeholder {
  color: rgba(1, 28, 64, 0.7);
}
.form__input input:not([type=checkbox]):hover, .form__input input:not([type=checkbox]):focus {
  outline-color: #29a3cc;
  box-shadow: 0 0 12px #f9fbfd;
}
.form__input input:not([type=checkbox]):active, .form__input input:not([type=checkbox]):focus-within {
  background-color: #29a3cc;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__input input:not([type=checkbox]) {
    padding-bottom: 13px;
  }
}
.form__input--checkbox label {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.form__input--checkbox input {
  flex-shrink: 0;
  position: relative;
  appearance: none;
  display: block;
  width: 18px;
  height: 18px;
  margin-right: auto;
  background-color: #f9fbfd;
}
.form__input--checkbox input:checked::after {
  content: "";
  position: absolute;
  left: 36%;
  top: 12%;
  width: 6px;
  height: 10px;
  border: solid #101a56;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}
.form__input--checkbox input:hover, .form__input--checkbox input:focus-visible {
  outline-color: #29a3cc;
  box-shadow: 0 0 12px #f9fbfd;
}
@media (max-width: 767px) {
  .form__input--checkbox input {
    width: 20px;
    height: 20px;
    margin-right: 8px;
  }
}
@media (max-width: 767px) {
  .form__input--checkbox {
    margin-top: -2px;
  }
}
.form__input--submit {
  padding-top: 11px;
}
@media (max-width: 767px) {
  .form__input--submit {
    padding-top: 8px;
  }
}
.form__input.is-invalid input {
  border: 1px solid #ff1553;
}
.form__input.is-invalid span {
  color: #ff1553;
}
.form__input.is-invalid .input-message {
  position: absolute;
  left: 0;
  top: calc(100% + 1px);
}

.form__submit-button {
  user-select: none;
  width: 100%;
  padding: 11px 40px;
  display: inline-block;
  border: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
}
.form__submit-button:hover {
  box-shadow: 0 0 12px #f9fbfd;
}
.form__submit-button:focus-visible {
  outline-color: #29a3cc;
  box-shadow: 0 0 12px #f9fbfd;
  border-radius: 3px;
}
.form__submit-button:active {
  border-radius: 3px;
  background-color: #29a3cc;
  color: #f9fbfd;
}

.contacts {
  padding-top: 120px;
  padding-bottom: 120px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}
@media (max-width: 1023px) {
  .contacts {
    padding-top: 100px;
    padding-bottom: 100px;
    gap: 27px;
  }
}
@media (max-width: 767px) {
  .contacts {
    padding-top: 59px;
    padding-bottom: 74px;
    grid-template-columns: 1fr;
  }
}

.contacts__map {
  position: relative;
  height: 306px;
  background-color: #011c40;
}
.contacts__map img {
  position: absolute;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
@media (max-width: 1023px) {
  .contacts__map {
    height: 335px;
  }
}
@media (max-width: 767px) {
  .contacts__map {
    height: 316px;
  }
}

.contacts__text {
  font-family: "Montserrat", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #011c40;
  padding-top: 75px;
  padding-left: 65px;
}
.contacts__text h2 {
  margin: 0;
  margin-bottom: 32px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 1023px) {
  .contacts__text h2 {
    margin-bottom: 28px;
  }
}
@media (max-width: 767px) {
  .contacts__text h2 {
    margin-bottom: 26px;
  }
}
@media (max-width: 1023px) {
  .contacts__text {
    padding-top: 66px;
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .contacts__text {
    padding-top: 0;
    order: -1;
  }
}

.contacts__list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 23px;
}
@media (max-width: 1023px) {
  .contacts__list {
    gap: 21px;
  }
}

.contacts__item,
.contacts__link {
  display: flex;
}
.contacts__item svg,
.contacts__link svg {
  margin-right: 10px;
  flex-shrink: 0;
}
@media (max-width: 1023px) {
  .contacts__item svg,
.contacts__link svg {
    margin-top: 2px;
  }
}

@media (max-width: 1023px) {
  .contacts__item {
    padding-right: 36px;
  }
  .contacts__item--adress svg {
    margin-top: 4px;
  }
}
@media (max-width: 767px) {
  .contacts__item {
    padding-right: 0;
  }
  .contacts__item--adress svg {
    margin-top: 5px;
  }
}

.contacts__link {
  user-select: none;
}
.contacts__link span {
  color: #011c40;
}
.contacts__link:hover {
  opacity: 0.6;
}
.contacts__link:focus-visible {
  border-radius: 3px;
  outline: none;
  box-shadow: 0 0 12px #011c40;
}
.contacts__link:active {
  box-shadow: none;
  opacity: 1;
}

.footer {
  background-color: #011c40;
  width: 100%;
}

.footer__navigation {
  max-width: 1250px;
  padding: 50px 0 43px;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .footer__navigation {
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 37px;
  }
}

.social__list {
  margin: 0;
  padding: 0;
  max-width: 120px;
  margin-top: 7px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 15px;
  transform: translateX(3px);
  list-style-type: none;
}
@media (max-width: 1023px) {
  .social__list {
    margin-top: 0;
    gap: 18px;
  }
}
@media (max-width: 767px) {
  .social__list {
    justify-content: flex-start;
    transform: translateX(-5px);
    gap: 16px;
  }
}

.social__link {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social__link:focus, .social__link:hover {
  opacity: 0.7;
}
.social__link svg {
  fill: #f9fbfd;
}
.social__link svg:active {
  fill: #29a3cc;
}
@media (max-width: 1023px) {
  .social__link {
    width: 20px;
    height: 20px;
  }
  .social__link svg {
    width: 17px;
    height: 16px;
  }
  .social__link--ok svg {
    width: 10px;
  }
}

